<template>
  <div id="app">
    <div id="head">
      <header>
        <img src="./assets/header.png" alt="Vue.js PWA" class="header-bar">
      </header>
    </div>
    <main>
      <img src="./assets/dslogo.png" alt="Vue.js PWA">
      <router-view></router-view>
    </main>
  </div>
</template>

<script>
export default {
  name: 'app'
}
</script>

<style>
body {
  margin: 0;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

img {
  width: 100%;
}

main {
  text-align: center;
}

#head { 
  border-top:solid 1px #cdcdcd;
  border-bottom:solid 1px #dedede;
  margin-top:4px;
  width:100%;
  z-index:10000;
  background-color:#efefef;
  position: absolute;
}

.header-bar { 
  padding-top: 10px;
  height: 36px;
  width: 100%;
  max-height: 36px;
}
header { 
  width:100%;
  margin: 0;
  height: 56px;
  color: #999;
  margin-top:8px auto;
  padding: 0px auto;
  text-align: center;
}

</style>



