<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    {{appState}}
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'hello',
  data () {
    return {
      msg: 'Welcome to Your Vue.js PWA'
    }
  },
  mounted: function () {
    this.$store.commit('setAppState', '/')
  },
  computed: {
    ...mapGetters([
      'appState'
    ]),
    ...mapActions([
      'setAppState'
    ])
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #35495E;
}
</style>



